// функция для добавления маски в калькулятор; name = класс и имя инпута
function maskingCalc(name, min, max){

    let $inputMask = $('.main-calc__input.'+name+' input');

    if($inputMask){
        $inputMask.inputmask({
            'alias': 'decimal',
            'groupSeparator': ' ',
            rightAlign: false,
            showMaskOnHover: false,
            'autoGroup': true,
            'min': min,
            'max': max
        });
    }
}
function maskingCalc2(name, min, max){

    let $inputMask = $('.mobcalc__step-content .main-calc__input input[name='+name+']');

    if($inputMask){
        $inputMask.inputmask({
            'alias': 'decimal',
            'groupSeparator': ' ',
            rightAlign: false,
            showMaskOnHover: false,
            'autoGroup': true,
            'min': min,
            'max': max
        });
    }
}

// функция для удаления маски из калькулятора
function unmaskingCalc(name){
    let $inputMask = $('.main-calc__input.'+name+' input');

    $inputMask.inputmask('remove').val('')
}

// анимация "Самого быстрого" маршрута
function animateS1() {
    $('#s1l1mask path')[0].style.strokeDasharray = $('#s1l1')[0].getTotalLength();
    $('#s1l1mask path')[0].style.strokeDashoffset = $('#s1l1')[0].getTotalLength();

    if(typeof tl !== 'undefined') {
        tl.kill();
    }

    tl = new TimelineMax();

    tl.set('#s1p1', {
        yPercent: -80,
        ease: Circ.easeOut,
        opacity: .001,
        transformOrigin: 'center bottom'
    });
    tl.set('#s1p2', {
        yPercent: -80,
        ease: Circ.easeOut,
        opacity: .001,
        transformOrigin: 'center bottom'
    });
    tl.set("#s1fly", {
        opacity: 0.00001
    });

    tl
        .to('#s1p1', {
            duration: .3,
            opacity: 1
        })
        .to('#s1p1', {
            duration: .2,
            yPercent: 0,
        }, '<.2')
        .to('#s1p1', {
            duration: .1,
            scaleY: .6
        })
        .to('#s1p1', {
            duration: .15,
            scaleY: 1,
            yPercent: -30
        })
        .to('#s1p1', {
            duration: .2,
            yPercent: 0,
        })
        .to('#s1p1', {
            duration: .1,
            scaleY: .9
        })
        .to('#s1p1', {
            duration: .1,
            scaleY: 1
        });

    tl
        .to('#s1p2', {
            duration: .3,
            opacity: 1
        })
        .to('#s1p2', {
            duration: .2,
            yPercent: 0,
        }, '<.2')
        .to('#s1p2', {
            duration: .1,
            scaleY: .6
        })
        .to('#s1p2', {
            duration: .15,
            scaleY: 1,
            yPercent: -30
        })
        .to('#s1p2', {
            duration: .2,
            yPercent: 0,
        })
        .to('#s1p2', {
            duration: .1,
            scaleY: .9
        })
        .to('#s1p2', {
            duration: .1,
            scaleY: 1
        });


    tl.to($('#s1l1mask path')[0], .7, {
        strokeDashoffset: 0,
        ease: Linear.easeNone
    });

    var t2 = new TimelineMax({
        repeat: -1
    });


    t2.to("#s1fly", {
        duration: 5,

        ease: "power1.inOut",
        motionPath: {
            path: "#s1l1",
            align: "#s1l1",
            autoRotate: true,
            alignOrigin: [0.5, 0.5]
        }
    });
    t2.add("end", 0);
    t2.to("#s1fly", {
        duration: 0.001,
        ease: "Linear.easeNone",
        opacity: 1,
        scale: 0
    }, 'end');
    t2.to("#s1fly", {
        duration: 1,
        delay: .4,
        ease: "Linear.easeNone",
        scale: 1
    }, 'end');
    t2.to("#s1fly", {
        duration: 1,
        delay: 4,
        ease: "Linear.easeNone",
        scale: 0
    }, 'end');

    tl.add(t2, 2)


    ScrollTrigger.create({
        trigger: ".smart-map__step.step1",
        animation: tl,
        start: "top center+=300px",
        end: "top center",
        once: true,
    });
}

